<style lang="scss" src="./style.scss" scoped></style>
<template src="./template.html"></template>
<script>
export default {
  name: 'narrative',
  data() {
    return {
      settings: [],
      show: {}
    }
  },
  created() {
    fetch('./api/narrative.json')
      .then(res => {
        return res.json()
      })
      .then(data => {
        this.settings = data
      })
  },
  mounted() {
    this.show = this.settings[0]
  }
}
</script>
